.popup__form-submit {
    padding: 0;
    margin: 13px 0 37px 0;
    border: none;
    width: 358px;
    height: 50px;
    background: #000000;
    border-radius: 2px;
    font-weight: 400;
    font-size: 18px;
    line-height: 22px;
    text-align: center;
    color: #FFFFFF;
}

.popup__form-submit:hover {
    cursor: pointer;
    opacity: 0.8;
}

@media screen and (max-width: 580px) {
    .popup__form-submit {
        margin: 45px 0 25px;
        width: 238px;
        height: 46px;
        font-size: 14px;
        line-height: 17px;
    }
}